body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 100px;
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
}

.App-title {
  font-size: 2.5em;
}

.App-input {
  width: 500px;
  font-size: 35px;
  border: 1px solid #aaa;
  border-radius: 2px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.section {
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
  border-bottom: 1px solid #999;
  padding-bottom: 20px;
  position: relative;
}

h1 {
  text-align: left;
  font-size: 26px;
}

button {
  border: 1px solid #222;
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  -webkit-box-shadow: 0px 1px 2px #222;
          box-shadow: 0px 1px 2px #222;
}

p {
  text-align: left;
  font-size: 16px;
  color: #222;
  margin-left: 20px;
}

span {
  position: absolute;
  right: 0px;
  bottom: 25px;
}

span.green {
  color: rgb(11, 168, 11);
}

span.red {
  color: rgb(194, 3, 45);
}

.result {
  background-color: rgb(10, 0, 97);
  color: white;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100px;
  font-size: 30px;
  padding: 0;
}
.canvas {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
}

.canvas-column {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  padding-top: 40px;
}

canvas {
  border: 1px solid #222;
  max-width: 280px;
  margin: 0 auto;
}

.canvas-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-top: 10px;
}

.canvas-buttons button {
  width: 40%;
}

.prediction-column {
  font-size: 200px;
}


